// Style in this for HomePage.js

.Homepage{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
}

.hero-text{
    color: white;
    text-align: center;
    font-size: 3.2rem;
    span{
        color: #037FFF;
    }
}

.h-sub-text{
    text-align: center;
    font-size: 1rem;
}

.icons{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1.5rem 0;
    font-size: 1rem;

    .icon-holder{
        margin-right: 1rem;
        .icon{
            font-size: 1.5rem;
            color: #a4acc4;
            transition: all 0.4s ease-in-out;
        }

        .yt:hover{
            color: red;
        }
        .fb:hover{
            color: blue;
        }
        .gh:hover{
            color: purple;
        }
    }
}