// All styles in ImageSection.scss are for pages/ImageSection.js

.ImageSection{

    display: flex;
    margin-top: 5rem;
    margin-left: 5rem;
    margin-bottom: 4rem;
    color: white;
    .img{
        width: 80vh;
        height: 50vh;
        img{
            border-radius:2rem;
            width: 90%;
            height: 130%;
            object-fit: cover;
            
            &:hover{
                border: 2px solid #037FFF;
                transition: all .1s ease-in-out;
            }
        }
    }


    .about-info{
        font-size: 0.8rem;
        margin-left: 2rem;
        h4{
            font-size: 2rem ;
        }
    }

    

    @media screen and (max-width: 1216px) {
        .img{
            height: 40vh;
            width: 60vh;
            img{
                width: 90%;
                height: 130%;
                object-fit: cover;
            }
        }
        .about-info{
            margin-left: 0;
            margin-right: 50px;
            font-size: 0.8rem;
        }
    }

    @media screen and (max-width: 800px) {
        margin-right: auto;
        display: grid;
        grid-template-columns: repeat(1,1fr);
        .img{
            flex-direction: column;
            height: 50vh;
            width: 80vh;
            margin: auto;
            img{
                width: 90%;
                height: 130%;
                object-fit: cover;
            }
        }
        .about-info{
            margin: auto;
            margin-top: 8rem;
            font-size: 1rem;
        }
    }

    @media screen and (max-width: 600px) {
        margin-left: 2rem;
        // margin: auto;
        display: grid;
        grid-template-columns: repeat(1,1fr);
        .img{
            flex-direction: column;
            height: 40vh;
            width: 60vh;
            margin: auto;
            img{
                width: 90%;
                height: 130%;
                object-fit: cover;
            }
        }
        .about-info{
            margin: auto;
            margin-top: 8rem;
            font-size: 0.8rem;
        }
    }

    @media screen and (max-width: 420px) {
        margin-left: 2rem;
        // margin: auto;
        display: grid;
        grid-template-columns: repeat(1,1fr);
        .img{
            flex-direction: column;
            height: 40vh;
            width: 50vh;
            margin: auto;
            img{
                width: 90%;
                height: 130%;
                object-fit: cover;
            }
        }
        .about-info{
            margin: auto;
            margin-top: 8rem;
            font-size: 0.85rem;
        }
    }

    .about-details{
       margin-top: 1rem;
       font-size: 0.9rem;
       display: flex;
       p{
           padding: 0.3rem 0;
       }

       .right-section{
            margin-left: 2.3rem;
       }
    }
}


