// All styles in event.scss are for Pages/Events.js

.Events{
    
    margin-top: 5rem;
    margin-left: 2rem;
    
    .Event-Text{
        margin-top: 2rem;
        margin-left: 1rem;
        margin-right: 3rem;
        font-size: 1.3rem;
        font-family: 'M PLUS Rounded 1c', sans-serif;
    }
}



.Event{
    
    margin-top: 3rem;
    margin-left: 1rem;
    font-size: 1rem;
    width: 65%;
    background-color: #191D2B;
    border-left: 1px solid #2e344e;
    border-right: 1px solid #2e344e;
    border-bottom: 1px solid #2e344e;
    border-top: 8px solid #2e344e;
    transition: all .4s ease-in-out;

    &:hover{
        border-top: 8px solid #037FFF;
    }

    @media screen and (max-width: 580px){
        width: 70%;
    }
}


.Event-content{
    padding: 1.1rem;
    padding-top: 0.5rem;
    color: #f3f9fb;
    img{
        height: 5rem;
        width: 5rem;
    }
    .Event-title{
        color: white;
        font-size: 1.2rem;
        font-weight: 500;
        position: relative;
        padding-bottom: 1rem;
        margin: 1rem 0;
        &::after{
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            width: 30%;
            height: 1px;
            background-color: #2e344e;
        }
    }

}

.Event-container{
    display: grid;
    margin-top: 2rem;
    margin-left: 2rem;
    grid-template-columns: repeat(3, 1fr);

    @media screen and (max-width: 1270px){
        grid-template-columns: repeat(2, 1fr);
        margin-left: 2rem;
    }

    @media screen and (max-width: 800px){
        grid-template-columns: repeat(1, 1fr);
        width: 100%;
        margin: 2rem 0rem;
        padding-left:8rem ;
    }

    @media screen and (max-width: 580px){
        grid-template-columns: repeat(1, 1fr);
        width: 100%;
        margin: 2rem 0rem;
        padding-left:3rem ;
    }
}