// All styles in event.scss are for Pages/Events.js

.Awards{
    
    margin-top: 5rem;
    margin-bottom: 2rem;

    .Award-Text{
        margin-top: 2rem;
        margin-left: 3rem;
        margin-right: 3rem;
        font-size: 1rem;
        font-family: 'M PLUS Rounded 1c', sans-serif;
    }
}



.Award{
    
    margin-top: 3rem;
    margin-left: 4.5rem;
    font-size: 1rem;
    width: 65%;
    background-color: #191D2B;
    border-left: 1px solid #2e344e;
    border-right: 1px solid #2e344e;
    border-bottom: 1px solid #2e344e;
    border-top: 8px solid #2e344e;
    transition: all .4s ease-in-out;
    
    &:hover{
        border-top: 8px solid #037FFF;
    }

    @media screen and (max-width: 1270px){
        width: 70%;
    }
}

.Award-content{
    padding: 1.1rem;
    padding-top: 0.5rem;
    img{
        height: 6rem;
        width: 6rem;
    }
    .Award-title{
        color: white;
        font-size: 1.2rem;
        font-weight: 500;
        position: relative;
        padding-bottom: 1rem;
        margin: 1rem 0;
        &::after{
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            width: 30%;
            height: 1px;
            background-color: #2e344e;
        }
    }

}

.Award-container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);

    @media screen and (max-width: 1270px){
        grid-template-columns: repeat(2, 1fr);
        margin-left: 2rem;
    }

    @media screen and (max-width: 800px){
        grid-template-columns: repeat(1, 1fr);
        width: 100%;
        margin: 2rem auto;
    }
}