.NavBar{
    height: 100vh;
    .nav{
        height: 100%;
        font-size: 1rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;

        @media screen and (max-width: 450px){
            font-size: 1rem;
        }
    }

    .profile{
        width: 100%;
        text-align: center;
        margin-top: 1rem;
        border-bottom: 1px solid #2e344e;    

        img{
            width: 70%;
            border-radius: 100%;
            border: 5px solid #2e344e;
            margin-bottom: 1rem;

            &:hover{
                border: 2px solid #037FFF;
                transition: all .4s ease-in-out;
            }
        }
    }

    .nav-items{
        width: 100%;
        .nav-item{
            list-style:none;
            text-align: center;
            a{
                display: block;
                text-decoration: none;
                font-size: inherit;
                color: white;
                padding: 0.5rem 0;
                position: relative;

                &::before{
                    content: "";
                    position: absolute;
                    top:0 ;
                    left: 0;
                    width: 0.4rem;
                    height: 100%;
                    background-color: #0381ff33;
                    transform-origin: bottom;
                    transform: scale(0);
                    transition: transform 0.4s, 0.2s;
                }
                &:hover::before{
                    width: 100%;
                    transform: scale(1);
                }
            }
        }
    }

    .footer{
        border-top:1px solid #2e344e;
        width: 100%;
        
        p{
            text-align: center;
            padding: 1rem 0;
        }
    }
}

.active{
    background-color: #037FFF;
}